import { FunctionComponent, useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { DataArticlesContext } from "../data/DataArticles";

export const Article: FunctionComponent = () => {
	const [userId, setUserId] = useState<number | undefined>();
	const { id } = useParams();
	const { articles, loadingError } = useContext(DataArticlesContext);

	useEffect(() => {
		if (!id) return;
		setUserId(parseInt(id));
	}, []);

	if (loadingError != "") {
		return <h1>{loadingError}</h1>;
	}

	if (typeof userId === "undefined") {
		return <></>;
	}

	return (
		<article className="flex flex-col flex-wrap max-w-2xl h-full">
			<section>
				<img
					className="max-h-96 object-cover"
					src={articles[userId].img}
					alt={articles[userId].alt}
				/>
				<div className="flex flex-col flex-1 pt-10 pb-10 px-8">
					<h2 className="leading-tight">{articles[userId].title}</h2>
					<h3 className="text-base">{articles[userId].category || "other"}</h3>
					<p className="mt-6">{`${articles[userId].description}`}</p>
				</div>
				<footer>
					<p>
						<label>
							<input type="checkbox" name="liked" /> Likes
						</label>
					</p>
					<p>
						<a href="#comments">35 komentaru</a>
					</p>
				</footer>
			</section>

			<section id="comments">
				<h2>Komente</h2>
				<form>
					<label htmlFor="comments_input">Zanechte koment</label>
					<textarea
						id="comments_input"
						name="comment"
						rows={4}
						placeholder="Koment..."
					></textarea>
					<input type="submit" value="Odeslat" />
				</form>
				<article>
					<header>
						<p>Miroslav Steiner</p>
						<span>-</span>
						<p>
							<time dateTime="2021-11-11T04:30">55 minutes ago</time>
						</p>
					</header>
					<p>
						Velmi podobn stroj vyrbli Ukrajinci jet snad 40 rok po eskm A38.
						Antonov 2 byl ideln dopravn prostedek v zemi s velmi dkou dopravn
						strukturou. V R by se uplatnil i dnes a u ns, jeho mal cestovn
						rychlost je vhodou pi pistvn teba na venkovskch letitch, na
						kad louce. Pi porech nebo velkch nehodch me tak nahradit
						vrtulnk, zejmna pi doprav zrannch nebo lkaskch zazen. Vozit
						pacienty by zvldl po nkolika najednou, nklady by byly nepochybn ni.
					</p>
					<footer>
						<p>
							<label>
								<input type="checkbox" />
							</label>
						</p>

						<button aria-label="Odpovdt na koment" type="button">
							Odpovdt
						</button>
					</footer>
				</article>
			</section>
		</article>
	);
};
